/*****************************
  Helper Classes
*****************************/

/* Text Color */
.text-primary {
  color: $primary !important;
}

.text-dark {
  color: $gray-9 !important;
}

.text-blue{
  color: $gray-8 !important;
}

.text-light {
  color: $gray-1 !important;
}

.text-muted {
  color: $gray-7 !important;
}

/* Background Color */
.bg-primary {
  background: $primary !important;
}

.bg-dark {
  background: $gray-9 !important;
}
.bg-blue{
  background: $gray-8 !important;
}

.bg-gray {
  background: $gray-2 !important;
}

.bg-light {
  background: $gray-1 !important;
}

/* Page section margin padding */
.space-lg-ptb{
 padding: 140px 0;
}
.space-lg-pt {
  padding-top: 140px ;
}
.space-lg-pb {
  padding-bottom:140px;
}
.space-ptb {
  padding: 100px 0;
}
.space-pt {
  padding-top: 100px ;
}
.space-pb {
  padding-bottom:100px;
}
.space-sm-ptb {
  padding: 70px 0;
}
.space-sm-pt {
  padding-top: 70px;
}
.space-sm-pb {
 padding-bottom:70px;
}
.space-xs-ptb {
  padding: 50px 0;
}
.space-xs-pt {
  padding-top: 50px;
}
.space-xs-pb {
 padding-bottom:50px;
}
.space-lg-mt{
  margin-top: 150px;
}

/* position */
.top-0{
  top: 0;
}
.bottom-0{
  bottom: 0;
}
.left-0{
  left: 0;
}
.right-0{
  right: 0;
}

.position-center {
  top: 50%;
  left: 50%;
  transform: translate(-50% , -50%);
}

.top-50 {
  top: 50%;
  transform: translateY(-50%);
}

.left-50 {
  left: 50%;
  transform: translateX(-50%);
}

/* z index */
.z-index-1 {
  z-index: 1;
}

.z-index-9 {
  z-index: 9;
}

.z-index-99 {
  z-index: 99;
}

.z-index-999 {
  z-index: 999;
}
.z-index-n1 {
  z-index: -1;
}

/* Opacity */
.opacity-10{
  opacity: 0.1;
}
.opacity-20{
  opacity: 0.2;
}
.opacity-30{
  opacity: 0.3;
}
.opacity-40{
  opacity: 0.4;
}
.opacity-50{
  opacity: 0.5;
}
.opacity-60{
  opacity: 0.6;
}
.opacity-70{
  opacity: 0.7;
}
.opacity-80{
  opacity: 0.8;
}
.opacity-90{
  opacity: 0.9;
}
.opacity-100{
  opacity: 1;
}


/* Font Size */
.display-1 {
  font-size: 9rem;
  line-height: 1;
  font-weight: 800;
}

.display-2 {
  font-size: 8rem;
  line-height: 1.2;
  font-weight: 800;
}

.display-3 {
  font-size: 7rem;
  line-height: 1.2;
  font-weight: 800;
}

.display-4 {
  font-size: 6rem;
  line-height: 1.2;
  font-weight: 800;
}

.display-5 {
  font-size: 5rem;
  line-height: 1.2;
}

.display-6 {
  font-size: 4rem;
  line-height: 1.2;
}

.display-7 {
  font-size: 3rem;
  line-height: 1.2;
}

.display-8 {
  font-size: 2.5rem;
  line-height: 1.2;
}

.display-9 {
  font-size: 1.5rem;
  line-height: 1.4;
}

.display-10 {
  font-size: 1rem;
  line-height: 1.4;
}

/* Overflow hidden */
.overflow-y-hidden{
  overflow-y: hidden;
}
.overflow-x-hidden{
  overflow-x: hidden;
}

/* border-radius */
.b-radius{
  border-radius:$border-radius;
}

.b-radius-none {
  border-radius: 0 !important;
}
.b-radius-top-none {
  border-radius: 0px 0px 3px 3px;
}
.b-radius-bottom-none {
  border-radius: 3px 3px 0px 0px;
}

.b-radius-left-none {
  border-radius: 0 3px 3px 0;
}

.b-radius-right-none {
  border-radius: 3px 0 0 3px;
}

/* box-shadow */

.box-shadow{
  box-shadow:$box-shadow;
}
.box-shadow-sm{
  box-shadow:$box-shadow-sm;
}
.boxshadow-lg{
  box-shadow:$boxshadow-lg;
}
.box-shadow-none{
  box-shadow:none !important;
}

