/* avatar */
.avatar {
  width: 50px;
  height: 50px;
  display: inline-block;
}

.avatar.avatar-sm {
  width: 20px;
  height: 20px;
}

.avatar.avatar-md {
  width: 80px;
  height: 80px;
}

.avatar.avatar-lg {
  width: 145px;
  height: 145px;
}

.avatar-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}


/* grayscale */
.grayscale {
    filter: grayscale(100%);
    transition: all 0.3s ease-in-out;
}
.grayscale:hover {
    filter: inherit;
}

/* pagination */
.pagination {
  .page-link {
    color: $primary;
  }

}

/* Contact-info */
.contact-info{
  ul{
    li{
      display: flex;
      align-items: flex-start;
      margin-bottom: 15px;
      &:last-child{
        margin-bottom: 0;
      }

      span{
        margin-left: 15px;
      }
      i{
        margin-top: 5px;
      }
    }
  }
}

  .post-navigation{
    .nav-previous{
      a{
        padding: 0;
        text-align: left;
        span{
          margin-left: 0;
          margin-right: 15px;
          &:before{
            right: auto;
            left: 0;
            transform: rotate(-45deg);
          }
          &:after{
            right: auto;
            left: 0;
            transform: rotate(45deg);
          }
        }
        &:hover{
          color: $primary;
        }
        &:focus{
          color: $primary;
        }
        &:active{
          color: $primary;
        }
      }
    }
    .nav-next{
      a{
        padding: 0;
        text-align: right;
        &:hover{
          color: $primary;
        }
        &:focus{
          color: $primary;
        }
        &:active{
          color: $primary;
        }
      }
    }
    .nav-title{
      margin-bottom: 0;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      width: 200px;
      display: inline-block;
    }
    }

  .post-tags{
    a{
      color: $body-color;

      &:hover{
          color: $primary;
      }
    }
  }

